	module.exports = {"data":{"post":{"html":"<p>In an inspired moment of creative fiscal policy, Finance Minister Eichel yesterday (with the approval of his boss the Chancellor) proposed moving the German Unity Day holiday to always fall on Sunday. This year a number of paid holidays fall on the weekend, thus adding a couple of tenths to national GDP, so why not have have a holiday fall always on the weekend? Alas, the idea fell like a lead balloon full of rocks. Maybe Eichel should hire some of those Greek statisiticians to rewrite the German calendar. My idea: move Unity Day to the day after Ascension, a nominal work day when noone works anyway. </p>\n<p><strong>Update:</strong> <a href=\"http://fistfulofeuros.net/archives/000913.php\" title=\"A Fistful of Euros: Another Brick In The Wall.\">Tobias</a> thinks it was all a ruse by Schr&ouml;der to trick the <em>L&auml;nder</em> into giving up a holiday in May. Maybe so, and the Union is all so easy to trick these days, but it still must be nice to have a chump like Eichel to always be the fall guy for you when things go wrong.</p>\n<p><a title=\"Plan to Move Unity Day Sets Off Political Firestorm | Current Affairs | Deutsche Welle |\" href=\"http://www.dw-world.de/dw/article/0,1564,1386127,00.html\">(DW-Online: Plan to Move Unity Day Sets Off Political Firestorm)</a></p>","fields":{"title":"Party like it's 1989","date":"05 Nov 2004","url":"https://papascott.de/2004-11-05-party-like-its-1989/"}}},"pathContext":{"slug":"/2004-11-05-party-like-its-1989/","prev":{"fields":{"title":"Lower the Bar! Fire the Statisticians!","date":"05 Nov 2004","slug":"/2004-11-05-lower-the-bar-fire-the-statisticians/"}},"next":{"fields":{"title":"Sydney and Hannover","date":"06 Nov 2004","slug":"/2004-11-06-sydney-and-hannover/"}}}}

